import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, message, Col } from 'antd';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { getDisciplineBehaviors, deleteDisciplineBehavior } from 'services/DisciplineBehavior';

class ListTypeDisciplineBehaviorsPage extends Component {
  state = { reload: 0 };
  handleDelete = async id => {
    await deleteDisciplineBehavior(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'K hiu',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tn hnh vi',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: routeConstant.XU_LY_HOC_VU__HANH_VI_XU_LY_KY_LUAT__NHAP_DU_LIEU,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Hnh vi k lut">
        <Row>
          <Col span={24}>
            <CartHeadAction>
              <Link to={routeConstant.XU_LY_HOC_VU__HANH_VI_XU_LY_KY_LUAT__NHAP_DU_LIEU}>
                <BtnAddNew />
              </Link>
            </CartHeadAction>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getDisciplineBehaviors}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTypeDisciplineBehaviorsPage />
  </Page>
);



